.step3 {
  .box {
    .btn-primary {
      width: 200px;
      margin: 10px;
      height: 70px;
      white-space: inherit;
    }

    .btn-primary {
      color: #fff;
      border-color: gray;
      background-color: gray;

      &:hover {
        color: #000;
        border-color: gray;
        background-color: white;
      }
    }
  }
}

.step4 {
  padding-bottom: 70px;

  input[type="checkbox"], input[type="radio"] {
    &:after {
      position: inherit !important;
      display: inherit !important;
      width: 18px !important;
      margin-right: 3px;
    }
  }
  .myCheckbox{
    &.form-check {
      padding: 0;
    }
  }
  .hideCheckbox {
    input {
      display: none;
    }
    .btn-outline-info {
      padding: 15px;
      color: black;
      border-color: var(--primary-color);
      &.active, &:hover {
        color: white;
        border-color: var(--primary-color);
        background-color: var(--primary-color);
      }
    }
  }
}

